<template>
  <div>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1" route="/monitor">系统监控</el-menu-item>
      <el-menu-item index="2" route="/videos">Vs</el-menu-item>
    </el-menu>
    <div class="line"></div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      activeIndex: "1",
    };
  },
  mounted() {
    this.doRoute();
  },
  methods: {
    handleSelect(key) {
      this.activeIndex = key;
      this.doRoute();
    },
    doRoute() {
      switch (this.activeIndex) {
        case "1":
          this.$router.push("/monitor");
          break;
        case "2":
          this.$router.push("/videos");
          break;
      }
    },
  },
};
</script>

